import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "@/App";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent } from "@/components/ui/card";
import { toast } from "@/hooks/use-toast";
import { User, ArrowLeft, Lock, Mail, Phone, Calendar, MapPin, Users } from "lucide-react";
import { getCountries, getStatesByCountry, getCitiesByState } from "@/lib/locationData";

const ProfilePage = () => {
  const navigate = useNavigate();
  const { user, updateProfile } = useAuth();
  const [isEditing, setIsEditing] = useState(false);
  const [name, setName] = useState(user?.name || "");
  const [email, setEmail] = useState(user?.email || "");
  const [phoneNumber, setPhoneNumber] = useState(user?.phone_number || "");
  const [gender, setGender] = useState(user?.gender || "");
  const [birthdate, setBirthdate] = useState(user?.birthdate || "");
  const [country, setCountry] = useState(user?.country || "");
  const [state, setState] = useState(user?.state || "");
  const [city, setCity] = useState(user?.city || "");
  const [pin, setPin] = useState("");

  // Location data
  const [countries, setCountries] = useState<Array<{ name: string; code: string }>>([]);
  const [states, setStates] = useState<Array<{ name: string; code: string }>>([]);
  const [cities, setCities] = useState<Array<{ name: string; id: string }>>([]);

  // Load countries on mount
  useEffect(() => {
    const countriesList = getCountries();
    setCountries(countriesList);
  }, []);

  // Load states when country changes
  useEffect(() => {
    if (country) {
      const statesList = getStatesByCountry(country);
      setStates(statesList);
      setState(""); // Reset state when country changes
      setCity(""); // Reset city when country changes
    } else {
      setStates([]);
      setCities([]);
    }
  }, [country]);

  // Load cities when state changes
  useEffect(() => {
    if (state && country) {
      const citiesList = getCitiesByState(country, state);
      setCities(citiesList);
      setCity(""); // Reset city when state changes
    } else {
      setCities([]);
    }
  }, [state, country]);

  // Update form values when user changes
  useEffect(() => {
    if (user) {
      setName(user.name || "");
      setEmail(user.email || "");
      setPhoneNumber(user.phone_number || "");
      setGender(user.gender || "");
      setBirthdate(user.birthdate || "");
      setCountry(user.country || "");
      setState(user.state || "");
      setCity(user.city || "");
    }
  }, [user]);

  const handleSave = () => {
    if (!name.trim()) {
      toast({
        title: "Invalid Input",
        description: "Name is required",
        variant: "destructive"
      });
      return;
    }

    if (pin.length > 0 && pin.length !== 4) {
      toast({
        title: "Invalid Input",
        description: "PIN must be exactly 4 digits",
        variant: "destructive"
      });
      return;
    }

    // Email validation if provided
    if (email && !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
      toast({
        title: "Invalid Input",
        description: "Please enter a valid email address",
        variant: "destructive"
      });
      return;
    }

    updateProfile({ 
      name: name.trim(),
      email: email.trim() || undefined,
      gender: gender || undefined,
      birthdate: birthdate || undefined,
      country: country || undefined,
      state: state || undefined,
      city: city || undefined,
      ...(pin.length === 4 && { pin })
    });
    setIsEditing(false);
    setPin("");
    toast({
      title: "Profile Updated",
      description: "Your profile has been successfully updated"
    });
  };

  const handleCancel = () => {
    setIsEditing(false);
    setName(user?.name || "");
    setEmail(user?.email || "");
    setPhoneNumber(user?.phone_number || "");
    setGender(user?.gender || "");
    setBirthdate(user?.birthdate || "");
    setCountry(user?.country || "");
    setState(user?.state || "");
    setCity(user?.city || "");
    setPin("");
  };


  return (
    <div className="min-h-screen bg-gradient-to-br from-karma-cream via-background to-karma-light-gold">
      {/* Header */}
      <div className="bg-white/80 backdrop-blur-sm shadow-sm border-b border-gray-200/50 sticky top-0 z-40 safe-area-top">
        <div className="max-w-md mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <button
              onClick={() => {
                if (window.history.length > 1) {
                  navigate(-1);
                } else {
                  navigate('/');
                }
              }}
              aria-label="Go back"
              title="Go back"
              className="p-2 hover:bg-gray-100 rounded-full transition-colors"
            >
              <ArrowLeft className="w-6 h-6 text-gray-600" />
            </button>
            <div className="text-center">
              <h1 className="text-xl font-bold text-gray-800">Profile</h1>
            </div>
            <div className="w-10" />
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="p-4 space-y-6 gesture-safe-bottom">
        {/* Profile Card */}
        <Card className="shadow-lg border-0">
          <CardContent className="pt-6 space-y-4">
            {!isEditing ? (
              <>
                <div className="text-center space-y-2">
                  <h2 className="text-2xl font-bold text-foreground">{user?.name || 'User'}</h2>
                  <p className="text-muted-foreground">Skincare Enthusiast</p>
                </div>
                
                <Button 
                  onClick={() => setIsEditing(true)}
                  className="w-full bg-gradient-to-r from-green-500 to-blue-500 text-white hover:from-green-600 hover:to-blue-600"
                >
                  <User className="w-4 h-4 mr-2" />
                  Edit Profile
                </Button>
              </>
            ) : (
              <div className="space-y-4">
                {/* Name */}
                <div>
                  <label className="text-sm font-medium mb-2 block text-gray-700">Name *</label>
                  <div className="relative">
                    <User className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
                    <Input
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                      placeholder="Enter your name"
                      className="w-full pl-10"
                    />
                  </div>
                </div>

                {/* Email */}
                <div>
                  <label className="text-sm font-medium mb-2 block text-gray-700">Email</label>
                  <div className="relative">
                    <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
                    <Input
                      type="email"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      placeholder="Enter your email"
                      className="w-full pl-10"
                    />
                  </div>
                </div>

                {/* Phone Number (Read-only) */}
                <div>
                  <label className="text-sm font-medium mb-2 block text-gray-700">Phone Number</label>
                  <div className="relative">
                    <Phone className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
                    <Input
                      value={phoneNumber}
                      disabled
                      className="w-full pl-10 bg-gray-50 text-gray-500 cursor-not-allowed"
                    />
                  </div>
                  <p className="text-xs text-gray-500 mt-1">Phone number cannot be changed</p>
                </div>

                {/* Gender */}
                <div>
                  <label className="text-sm font-medium mb-2 block text-gray-700">Gender</label>
                  <div className="relative">
                    <Users className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400 z-10" />
                    <select
                      value={gender}
                      onChange={(e) => setGender(e.target.value)}
                      className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 appearance-none bg-white"
                    >
                      <option value="">Select Gender</option>
                      <option value="male">Male</option>
                      <option value="female">Female</option>
                      <option value="other">Other</option>
                      <option value="prefer-not-to-say">Prefer not to say</option>
                    </select>
                  </div>
                </div>

                {/* Birthdate */}
                <div>
                  <label className="text-sm font-medium mb-2 block text-gray-700">Date of Birth</label>
                  <div className="relative">
                    <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
                    <Input
                      type="date"
                      value={birthdate}
                      onChange={(e) => setBirthdate(e.target.value)}
                      className="w-full pl-10"
                      max={new Date().toISOString().split('T')[0]}
                    />
                  </div>
                </div>

                {/* Location */}
                <div className="space-y-3">
                  <label className="text-sm font-medium block text-gray-700">Location</label>
                  
                  {/* Country */}
                  <div className="relative">
                    <MapPin className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400 z-10" />
                    <select
                      value={country}
                      onChange={(e) => setCountry(e.target.value)}
                      className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 appearance-none bg-white"
                    >
                      <option value="">Select Country</option>
                      {countries.map((c) => (
                        <option key={c.code} value={c.name}>
                          {c.name}
                        </option>
                      ))}
                    </select>
                  </div>

                  {/* State */}
                  {states.length > 0 && (
                    <div className="relative">
                      <select
                        value={state}
                        onChange={(e) => setState(e.target.value)}
                        className="w-full pl-4 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 appearance-none bg-white"
                      >
                        <option value="">Select State</option>
                        {states.map((s) => (
                          <option key={s.code} value={s.name}>
                            {s.name}
                          </option>
                        ))}
                      </select>
                    </div>
                  )}

                  {/* City */}
                  {cities.length > 0 && (
                    <div className="relative">
                      <select
                        value={city}
                        onChange={(e) => setCity(e.target.value)}
                        className="w-full pl-4 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500 appearance-none bg-white"
                      >
                        <option value="">Select City</option>
                        {cities.map((c) => (
                          <option key={c.id} value={c.name}>
                            {c.name}
                          </option>
                        ))}
                      </select>
                    </div>
                  )}
                </div>
                
                {/* Change PIN */}
                <div>
                  <label className="text-sm font-medium mb-2 block text-gray-700">Change PIN</label>
                  <div className="relative">
                    <Lock className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
                    <Input
                      type="password"
                      value={pin}
                      onChange={(e) => {
                        const value = e.target.value.replace(/\D/g, '').slice(0, 4);
                        setPin(value);
                      }}
                      placeholder="Enter 4-digit PIN"
                      maxLength={4}
                      className="text-center tracking-widest pl-10"
                      inputMode="numeric"
                    />
                  </div>
                  <p className="text-xs text-gray-500 mt-1">Leave empty to keep current PIN</p>
                </div>
                
                {/* Action Buttons */}
                <div className="flex gap-2 pt-2">
                  <Button 
                    onClick={handleSave}
                    className="flex-1 bg-gradient-to-r from-green-500 to-blue-500 text-white hover:from-green-600 hover:to-blue-600"
                  >
                    Save Changes
                  </Button>
                  <Button 
                    variant="outline" 
                    onClick={handleCancel}
                    className="flex-1"
                  >
                    Cancel
                  </Button>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default ProfilePage;